/* @import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap'); */
@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap");

.box {
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
}
.intro {
  display: flex;
  width: 80vw;
  justify-content: space-around;
  margin: 100px auto;
}
.intro > .text {
  font-family: Open Sans;
  align-self: flex-end;
}
.intro > .text > h3,
h1 {
  text-align: center;
}
.intro > .text > h3 {
  margin-top: 20px;
}
.box > .download {
  display: flex;
  width: 80vw;
  flex-direction: row;
  justify-content: center;
  margin: 0 auto;
}

.download > .input > button {
  background: #ff735c;
  border: none;
  color: #ffffff;
  width: 129px;
  cursor: pointer;
}
.download > .input {
  width: 502px;
  height: 57px;

  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  margin-right: 20px;
}
.download > span {
  margin-right: 20px;
}
.download > .input > div {
  display: flex;
  width: 350px;
  align-items: center;
  border: 1px solid #888888;
  border-radius: 5px;
  height: 57px;
  box-sizing: border-box;
  justify-content: space-around;
}

.download > .input > div > input {
  height: 70%;
  width: 80%;
  outline: none;
  font-size: 17px;
  border: none;
  margin: 5px;
}

@media (max-width: 768px) {
  .intro {
    width: 100vw;
    position: relative;
  }
  .intro > .text {
    width: 98vw;
    position: absolute;
  }
  .download > .input {
    width: 94vw;
    display: flex;
    height: 114px;
    margin: auto;
    flex-direction: column;
    /* justify-content: space-between;
   */
  }
  .download > .input > button {
    width: 100%;
    border-radius: 5px;

    height: 57px;
    /* justify-content: space-between;
   */
  }
  .download > .input > div{
    width: 100%;
    height: 57px;
    margin: 10px 0;
    /* justify-content: space-between;
   */
  }
  .box > .download {
    display: flex;
    flex-direction: column;
    width: 100vw;
  }
  .box>.download > span {
  margin: 10px 0;
}
.post-office>img:nth-child(1){
  display: none;
}
.post-office>img{
  width:100vw;

}

.intro > .right-svg>img{
  width:200px;

}
}
