.testimonial-header {
  background: #ff735c;
  text-align: center;
  color: #fff;
  height: 50px;
  font-family: Open Sans;
  font-style: normal;
  font-weight: bold;
  font-size: 28px;
  margin: 50px 0;
  line-height: 50px;
}
.testimonials {
  display: flex;
  justify-content: space-around;
}
.testimonials > .testimonial-card {
  /* flex: 1; */
  font-family: Open Sans;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 16px;

  color: #787878;
  width: 300px;
}
.left-quote {
  margin: 20px;
}
.right-quote {
  margin: 20px;
  float: right;
}
.testimonial-svg {
  clear: right;
  display: block;
  margin: auto;
}
.user {
  display: flex;
  align-items: center;
  width: 90%;
  margin: 20px 0;
  justify-content: space-between;
}
.user > img {
  border-radius: 50%;
}
@media (max-width: 768px) {
  .testimonials {
    display: flex;
    flex-direction: column;
    width: 100vw;
  }
  .testimonials > .testimonial-card {
    /* flex: 1; */
    margin: auto;
  }
  .testimonial-header {
    width: 100%;
  }
  .right-quote,
  .left-quote {
    display: none;
  }
  .testimonial-svg{
    width: 100%;
  }
}
