.nav-box > .logo {
  background-image: url("logo.png");
  width: 100px;
  height: 60px;
  background-size: contain;
  background-repeat: no-repeat;
  margin-left: 3vw;
}
.nav-box {
  box-sizing: border-box;
  max-width: 100vw;
  height: 80px;
  position: relative;
  display: flex;
  justify-content: space-between;
  background: #ffffff;
  align-items: center;
  border-bottom: 2px solid #dedede;
}
.nav-box > button {
  display: none;
  border: none;
  cursor: pointer;
  background-color: transparent;
}

.nav {
  height: 100%;
  display: flex;
  justify-content: space-around;
  /* width: 30vw; */
  align-items: center;
  color: #000;

  margin-right: 50px;
  font-family: Open Sans;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
}
.link {
  color: inherit;
  text-decoration: none;
  margin: 20px;
}
.active-link {
  background-color: #fff;
  color: #000;
  padding: 10px;
  border-radius: 5px;
}

@media (max-width: 768px) {
  .nav-box > button {
    display: initial;
    margin-right: 20px;
  }
  .nav {
    display: none;
  }
  .active {
    position: absolute;
    top: 80px;
    left: 0;
    width: 100vw;
    height: 30vh;
    z-index: 10;
    display: initial;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content:flex-start;
    background-color: #fff;
    /* align-self: flex-end; */
    /* justify-content: ; */
  }
}
