.privacy-header {
  background: #ff735c;
  text-align: center;
  color: #fff;
  height: 50px;
  font-family: Open Sans;
  font-style: normal;
  font-weight: bold;
  font-size: 28px;
  margin: 50px 0;
  line-height: 50px;
}
.privacy {
  width: 60vw;
  font-family: sans-serif;
  font-style: normal;
  font-weight: bold;

  margin: auto;
}
.privacy > h2 {
  margin: 20px auto;
}
.privacy > p {
  font-size: 16px;
  line-height: 24px;
}
.privacy-footer {
  margin-top: 100px;
  position: relative;
}
.social {
  height: 250px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  color: #fff;
}
.social > img {
  width: 50px;
  height: 50px;
}
.social-icons {
  width: 200px;
  display: flex;
  justify-content: space-between;
}
.download-card > .input {
  display: flex;
  align-items: center;
}
.download-card > .input > button {
  background: #ea685e;
  border-radius: 0px 5px 5px 0px;
  width: 129px;
  height: 57px;
  border: none;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 22px;
  color: #ffffff;
  cursor: pointer;
}
.ext {
  width: 38px;
  height: 57px;
  background: #c4c4c4;
  border-radius: 5px 0px 0px 5px;
  text-align: center;
  line-height: 57px;
}

.input > input {
  width: 432px;
  height: 57px;

  background: #ffffff;
  border: 1px solid #888888;
  box-sizing: border-box;
  font-size: 20px;
}

.download-card{
width: 517px;
height: 147px;
background: #FFFFFF;
border-radius: 5px;
display: flex;
flex-direction:column;
justify-content: space-around;
align-self: center;
padding:10px;

}
.download-card>.header>h3{
  margin: 10px 0;
}
.footer{
  position: absolute;
  left: 50%;
  bottom: 5%;
  transform:translate(-50%);
  display: flex;
  justify-content:space-around;
  width: 100%;
}


@media (max-width: 768px) {
  .privacy {
    width: 94vw;
  }
  .download-card {
    width: 70vw;
  }
  .input > input {
    width: 98%;
  }
  .social {
    display: flex;
    justify-content: flex-start;
  }
  .social-icons {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  .privacy-footer {
    width: 100vw;
    height: 300px;
  }
  .privacy-footer>img {
    width: 100%;
    height: 100%;
  }

} 