.download-header {
  background: #ff735c;
  text-align: center;
  color: #fff;
  height: 50px;
  font-family: Open Sans;
  font-style: normal;
  font-weight: bold;
  font-size: 28px;
  margin: 50px 0;
  line-height: 50px;
}
.mobile {
  display: flex;
  justify-content: space-around;
  align-items: flex-end;
  width: 80%;
  margin: 20px;
  height: 70vh;
}
.mobile > .mid {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: space-between;
  align-self: flex-start;
  height: 100%;
}
.download {
  display: flex;
  flex-direction: column;
  align-self: flex-end;
  align-items: center;
}
.download > h1 {
  margin: 20px;
}
.mid > img {
  margin: auto;
}
.mobile-box {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-between;
}
@media (max-width: 768px) {
  .mobile {
    display: flex;
    justify-content: space-around;
    align-items: flex-start;
    width: 100vw;
    margin: 0;
    height: 75vh;
  }
  .mobile > div {
    flex: 1;
  }
  .mobile > div > img {
    width: 60px;
  }
  .mobile > .mid {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    align-self: flex-end;
    height: 100%;
  }
}
