.About-header {
  background: #ff735c;
  text-align: center;
  color: #fff;
  height: 50px;
  font-family: Open Sans;
  font-style: normal;
  font-weight: bold;
  font-size: 28px;
  margin: 50px 0;
  line-height: 50px;
}
.about {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.about > p {
  width: 60vw;
  margin-bottom: 40px;
  font-family:  sans-serif;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 22px;
  letter-spacing: 0em;
}
@media(max-width:768px) {
  .about > p {
  width: 90vw;
  font-size: 16px;
  
}
.about>img {
  
  width: 90vw;
}
}