@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap);
*{
    margin:0;
    box-sizing: border-box;
    padding:0;
    scroll-behavior: smooth;
}
/* @import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap'); */

.box {
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
}
.intro {
  display: flex;
  width: 80vw;
  justify-content: space-around;
  margin: 100px auto;
}
.intro > .text {
  font-family: Open Sans;
  align-self: flex-end;
}
.intro > .text > h3,
h1 {
  text-align: center;
}
.intro > .text > h3 {
  margin-top: 20px;
}
.box > .download {
  display: flex;
  width: 80vw;
  flex-direction: row;
  justify-content: center;
  margin: 0 auto;
}

.download > .input > button {
  background: #ff735c;
  border: none;
  color: #ffffff;
  width: 129px;
  cursor: pointer;
}
.download > .input {
  width: 502px;
  height: 57px;

  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  margin-right: 20px;
}
.download > span {
  margin-right: 20px;
}
.download > .input > div {
  display: flex;
  width: 350px;
  align-items: center;
  border: 1px solid #888888;
  border-radius: 5px;
  height: 57px;
  box-sizing: border-box;
  justify-content: space-around;
}

.download > .input > div > input {
  height: 70%;
  width: 80%;
  outline: none;
  font-size: 17px;
  border: none;
  margin: 5px;
}

@media (max-width: 768px) {
  .intro {
    width: 100vw;
    position: relative;
  }
  .intro > .text {
    width: 98vw;
    position: absolute;
  }
  .download > .input {
    width: 94vw;
    display: flex;
    height: 114px;
    margin: auto;
    flex-direction: column;
    /* justify-content: space-between;
   */
  }
  .download > .input > button {
    width: 100%;
    border-radius: 5px;

    height: 57px;
    /* justify-content: space-between;
   */
  }
  .download > .input > div{
    width: 100%;
    height: 57px;
    margin: 10px 0;
    /* justify-content: space-between;
   */
  }
  .box > .download {
    display: flex;
    flex-direction: column;
    width: 100vw;
  }
  .box>.download > span {
  margin: 10px 0;
}
.post-office>img:nth-child(1){
  display: none;
}
.post-office>img{
  width:100vw;

}

.intro > .right-svg>img{
  width:200px;

}
}

.nav-box > .logo {
  background-image: url(data:image/png;base64,);
  width: 100px;
  height: 60px;
  background-size: contain;
  background-repeat: no-repeat;
  margin-left: 3vw;
}
.nav-box {
  box-sizing: border-box;
  max-width: 100vw;
  height: 80px;
  position: relative;
  display: flex;
  justify-content: space-between;
  background: #ffffff;
  align-items: center;
  border-bottom: 2px solid #dedede;
}
.nav-box > button {
  display: none;
  border: none;
  cursor: pointer;
  background-color: transparent;
}

.nav {
  height: 100%;
  display: flex;
  justify-content: space-around;
  /* width: 30vw; */
  align-items: center;
  color: #000;

  margin-right: 50px;
  font-family: Open Sans;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
}
.link {
  color: inherit;
  text-decoration: none;
  margin: 20px;
}
.active-link {
  background-color: #fff;
  color: #000;
  padding: 10px;
  border-radius: 5px;
}

@media (max-width: 768px) {
  .nav-box > button {
    display: inline;
    display: initial;
    margin-right: 20px;
  }
  .nav {
    display: none;
  }
  .active {
    position: absolute;
    top: 80px;
    left: 0;
    width: 100vw;
    height: 30vh;
    z-index: 10;
    display: initial;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content:flex-start;
    background-color: #fff;
    /* align-self: flex-end; */
    /* justify-content: ; */
  }
}

.first-row {
  display: flex;
  justify-content: space-around;
}
.second-row {
  display: flex;
  justify-content: space-around;
}
.features {
  display: flex;
  /* align-items: center; */
  flex-wrap: wrap;

}
.feature {
  flex-basis: 33.33%;
  width: 200px;
  height: 200px;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.offer-header {
  background: #ff735c;
  text-align: center;
  color: #fff;
  height: 50px;
  font-family: Open Sans;
  font-style: normal;
  font-weight: bold;
  font-size: 28px;
  margin: 50px 0;
  line-height: 50px;
}
@media (max-width: 768px){
  .feature{
    flex-basis: 50%;
  }

}
.download-header {
  background: #ff735c;
  text-align: center;
  color: #fff;
  height: 50px;
  font-family: Open Sans;
  font-style: normal;
  font-weight: bold;
  font-size: 28px;
  margin: 50px 0;
  line-height: 50px;
}
.mobile {
  display: flex;
  justify-content: space-around;
  align-items: flex-end;
  width: 80%;
  margin: 20px;
  height: 70vh;
}
.mobile > .mid {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: space-between;
  align-self: flex-start;
  height: 100%;
}
.download {
  display: flex;
  flex-direction: column;
  align-self: flex-end;
  align-items: center;
}
.download > h1 {
  margin: 20px;
}
.mid > img {
  margin: auto;
}
.mobile-box {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-between;
}
@media (max-width: 768px) {
  .mobile {
    display: flex;
    justify-content: space-around;
    align-items: flex-start;
    width: 100vw;
    margin: 0;
    height: 75vh;
  }
  .mobile > div {
    flex: 1 1;
  }
  .mobile > div > img {
    width: 60px;
  }
  .mobile > .mid {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    align-self: flex-end;
    height: 100%;
  }
}

.testimonial-header {
  background: #ff735c;
  text-align: center;
  color: #fff;
  height: 50px;
  font-family: Open Sans;
  font-style: normal;
  font-weight: bold;
  font-size: 28px;
  margin: 50px 0;
  line-height: 50px;
}
.testimonials {
  display: flex;
  justify-content: space-around;
}
.testimonials > .testimonial-card {
  /* flex: 1; */
  font-family: Open Sans;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 16px;

  color: #787878;
  width: 300px;
}
.left-quote {
  margin: 20px;
}
.right-quote {
  margin: 20px;
  float: right;
}
.testimonial-svg {
  clear: right;
  display: block;
  margin: auto;
}
.user {
  display: flex;
  align-items: center;
  width: 90%;
  margin: 20px 0;
  justify-content: space-between;
}
.user > img {
  border-radius: 50%;
}
@media (max-width: 768px) {
  .testimonials {
    display: flex;
    flex-direction: column;
    width: 100vw;
  }
  .testimonials > .testimonial-card {
    /* flex: 1; */
    margin: auto;
  }
  .testimonial-header {
    width: 100%;
  }
  .right-quote,
  .left-quote {
    display: none;
  }
  .testimonial-svg{
    width: 100%;
  }
}

