.first-row {
  display: flex;
  justify-content: space-around;
}
.second-row {
  display: flex;
  justify-content: space-around;
}
.features {
  display: flex;
  /* align-items: center; */
  flex-wrap: wrap;

}
.feature {
  flex-basis: 33.33%;
  width: 200px;
  height: 200px;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.offer-header {
  background: #ff735c;
  text-align: center;
  color: #fff;
  height: 50px;
  font-family: Open Sans;
  font-style: normal;
  font-weight: bold;
  font-size: 28px;
  margin: 50px 0;
  line-height: 50px;
}
@media (max-width: 768px){
  .feature{
    flex-basis: 50%;
  }

}