.contact-header {
  background: #ff735c;
  text-align: center;
  color: #fff;
  height: 50px;
  font-family: Open Sans;
  font-style: normal;
  font-weight: bold;
  font-size: 28px;
  margin: 50px 0;
  line-height: 50px;
}

.left-icon > div {
  display: flex;
  margin: 20px;

}
.left-icon > div > .name {
  margin: 10px;
}
.contact-box {
  display: flex;
  justify-content: space-around;
}
.contact-svg {
  display: flex;
  justify-content: center;
  margin: 50px;
}
.contact-box > .form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}
.contact-box > .form > .name {
  display: flex;
  width: 724px;
  justify-content: space-between;
}

.contact-box > .form > .name > input {
  width: 352px;
  height: 50px;
  background: #ffffff;
  border: 1px solid #787878;
  box-sizing: border-box;
  border-radius: 5px;
}
.contact-box > .form > input {
  width: 724px;
  height: 50px;

  background: #ffffff;
  border: 1px solid #787878;
  box-sizing: border-box;
  border-radius: 5px;
}
.contact-box > .form > button {
  width: 177px;
  height: 46px;
  background: #ea685e;
  border-radius: 10px;
  outline: none;
  border: none;
  cursor: pointer;
  font-family: Open Sans;
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 16px;
  color: #fff;
}


@media(max-width:768px){
  .contact-box > .form > input,.contact-box > .form > .name{
    width:96vw ;
  }
  .contact-box {
    display:flex;
    flex-direction: column-reverse;
    /* justify-content: */
  }
  .left-icon{
    margin: auto;
    display: flex;
    width: 96vw;
  }
  .left-icon > div {
  display: flex;
  margin: 20px 0;
  flex-direction: column;
  align-items:center;
  text-align: center;

}
  .left-icon > div>img {
 width: 50px;height: 50px;

}
.contact-svg{
  width: 100vw;
  margin:40px 0px;
}
.contact-box > .form>* {
  margin: 10px auto;
}
.contact-box > .form>.name>input{
  width: 45vw;
}
.contact-svg>img{

  width: 100%;
  

}

}